import * as layout from '../actions/layout';
import * as notification from '../actions/notification';
import * as bookingInquiry from '../../booking-inquiry/actions/booking-inquiry';

export interface State {
    loading: number;
    showNotification: boolean;
}

const initialState: State = {
    loading: 0,
    showNotification: false
};

export function reducer(state: State = initialState, action: layout.Actions): State {
    switch (action.type) {
        case layout.SHOW_LOADING:
        case bookingInquiry.GET_SERVICE_PACKAGE_DETAILS:
        case bookingInquiry.GET_BOOKING_INQUIRY:
        case bookingInquiry.SAVE_INQUIRY: {
            return { ...state, loading: state.loading + 1 };
        }

        case layout.HIDE_LOADING:
        case bookingInquiry.REPLACE_SERVICE_PACKAGE_DETAILS:
        case bookingInquiry.REPLACE_BOOKING_INQUIRY:
        case bookingInquiry.SAVE_INQUIRY_COMPLETE: {
            return { ...state, loading: state.loading - 1 };
        }

        case layout.SHOW_NOTIFICATION:
        case notification.SET: {
            return { ...state, showNotification: true };
        }

        case layout.HIDE_NOTIFICATION: {
            return { ...state, showNotification: false };
        }

        default: {
            return state;
        }
    }
}

export const getLoading = (state: State) => state.loading;



