import * as fromBookingInquiry from './booking-inquiry';
import { ActionReducerMap, createFeatureSelector, createSelector } from '@ngrx/store';

export interface State {
    bookingInquiries: fromBookingInquiry.State;
}

export const reducers: ActionReducerMap<State> = {
    bookingInquiries: fromBookingInquiry.reducer
};

export const getBookingInquiry = createFeatureSelector<fromBookingInquiry.State>('bookingInquiries');

export const getServicePackageList = createSelector(getBookingInquiry, fromBookingInquiry.getServicePackages);
export const getPastInquiries = createSelector(getBookingInquiry, fromBookingInquiry.getPastInquiries);
export const getSelectedPackages = createSelector(getBookingInquiry, fromBookingInquiry.getSelectedPackages);
export const getCurrentVIN = createSelector(getBookingInquiry, fromBookingInquiry.getCurrentVIN);
export const getIsCurrentInquirySaved = createSelector(getBookingInquiry, fromBookingInquiry.getIsCurrentInquirySaved);



