import { ValidatorFn, AbstractControl } from '@angular/forms';

export function firstSixCharsShouldBeLetters(): ValidatorFn {
    return (control: AbstractControl): { [key: string]: any } => {
        if (control.value.length < 17) {
            return null;
        }

        const firstSixCharacters = control.value.substring(0, 6);
        const isValid = firstSixCharacters.match('^[a-zA-Z]+$');
        return isValid ? null : { firstSixCharsError: { value: control.value } };
    };
}

export function lastSixCharsShouldBeNumbers(): ValidatorFn {
    return (control: AbstractControl): { [key: string]: any } => {
        if (control.value.length < 17) {
            return null;
        }

        const lastSixCharacters = control.value.substring(11, 17);
        const isValid = lastSixCharacters.match('^[0-9]+$');
        return isValid ? null : { lastSixCharsError: { value: control.value } };
    };
}

export function noSpecialSign(): ValidatorFn {
    return (control: AbstractControl): { [key: string]: any } => {
        if (control.value.length < 17) {
            return null;
        }

        const containsSpecialCharacters = control.value.match('[^A-Za-z0-9]+');
        return containsSpecialCharacters ? { noSpecialSignError: { value: control.value } } : null;
    };
}



