import { Component, OnInit } from '@angular/core';
import { CoreStateService } from '../../state/core/services/core-state.service';
import localeDe from '@angular/common/locales/de';
import { registerLocaleData } from '@angular/common';

@Component({
    selector: 'vwbt-app',
    template: `
        <vwbt-header></vwbt-header>

        <vwbt-search></vwbt-search>

        <vwbt-search-result></vwbt-search-result>

        <vwbt-loader [loading]="loading"></vwbt-loader>
    `
})
export class AppComponent implements OnInit {
    loading: number;

    constructor(private coreStateService: CoreStateService) {}

    ngOnInit() {
        registerLocaleData(localeDe);
        this.coreStateService.getLoading().subscribe(loading => (this.loading = loading));
    }
}



