import { Component } from '@angular/core';

@Component({
    selector: 'vwbt-header',
    template: `
        <header>
            <div class="container">
                <div class="row">
                    <div class="col">
                        <h1>
                            <img src="assets/icons8-car-50.png" style="width:100px;height:125px;" alt="Training">
                            <strong>Training</strong>
                            <sub>Booking & Inquiry Online Service</sub>
                        </h1>
                    </div>
                    <div class="col text-right">
                        <p class="avatar"></p>
                        <p class="username">Thomas Müller</p>
                        <p class="logout"><a href="#">Log out</a></p>
                    </div>
                </div>
            </div>
        </header>
    `,
    styles: [
        `
        header {
            background: #EFEFEF;
            border-bottom: 1px solid #5E89BB;
            height: 162px;
        }

        h1 {
            height: 162px;
        }

        h1 img {
            float: left;
            padding-top: 30px;
        }

        h1 strong {
            left: 20px;
            position: relative;
            top: 35px;
        }

        h1 sub {
            color: #555;
            display: block;
            font-size: 24px;
            left: 21px;
            top: 50px;
            position: relative;
        }

        .avatar {
            background: #bbb;
            border: 1px solid #999;
            border-radius: 100%;
            float: right;
            height: 64px;
            position: relative;
            top: 45px;
            width: 64px;
        }

        .username {
            font-size: 20px;
            position: relative;
            right: 20px;
            top: 48px;
        }

        .logout {
            font-size: 18px;
            position: relative;
            right: 20px;
            top: 33px;
        }
    `
    ]
})
export class HeaderComponent {}



