import { Action } from '@ngrx/store';
import { ServicePackage } from '../model/service-package';
import { BookingInquiry } from '../model/booking-inquiry';
import { NewInquiry } from '../model/new-inquiry';

const prefix = '[SERVICEPACKAGES]';

export const SET_CURRENT_VIN = `${prefix} Set current VIN`;

export const GET_BOOKING_INQUIRY = `${prefix} Get booking inquiries`;
export const RESET_BOOKING_INQUIRY = `${prefix} Reset booking inquiry`;
export const REPLACE_BOOKING_INQUIRY = `${prefix} Replace booking inquiries`;

export const GET_SERVICE_PACKAGE_DETAILS = `${prefix} Get service package details`;
export const REPLACE_SERVICE_PACKAGE_DETAILS = `${prefix} Replace service package details`;

export const ADD_SERVICE_PACKAGE_TO_INQUIRY = `${prefix} Add service package to inquiry`;
export const REMOVE_SERVICE_PACKAGE_FROM_INQUIRY = `${prefix} Remove service package from inquiry`;
export const RESET_SERVICE_PACKAGE_SELECTION = `${prefix} Reset service package selection`;

export const SAVE_INQUIRY = `${prefix} Save inquiry`;
export const SAVE_INQUIRY_COMPLETE = `${prefix} Save inquiry complete`;

export class SetCurrentVIN implements Action {
    readonly type = SET_CURRENT_VIN;

    constructor(public payload: string) {}
}

export class GetBookingInquiry implements Action {
    readonly type = GET_BOOKING_INQUIRY;

    constructor(public payload: string) {}
}

export class GetServicePackageDetails implements Action {
    readonly type = GET_SERVICE_PACKAGE_DETAILS;

    constructor(public payload: string) {}
}

export class ReplaceBookingInquiry implements Action {
    readonly type = REPLACE_BOOKING_INQUIRY;

    constructor(public payload: BookingInquiry) {}
}

export class ReplaceServicePackageDetails implements Action {
    readonly type = REPLACE_SERVICE_PACKAGE_DETAILS;

    constructor(public payload: ServicePackage) {}
}

export class AddServicePackageToInquiry implements Action {
    readonly type = ADD_SERVICE_PACKAGE_TO_INQUIRY;

    constructor(public payload: string) {}
}

export class RemoveServicePackageFromInquiry implements Action {
    readonly type = REMOVE_SERVICE_PACKAGE_FROM_INQUIRY;

    constructor(public payload: string) {}
}

export class ResetServicePackageSelection implements Action {
    readonly type = RESET_SERVICE_PACKAGE_SELECTION;
}

export class ResetBookingInquiry implements Action {
    readonly type = RESET_BOOKING_INQUIRY;

    constructor() {}
}

export class SaveInquiry implements Action {
    readonly type = SAVE_INQUIRY;

    constructor(public payload: NewInquiry) {}
}

export class SaveInquiryComplete implements Action {
    readonly type = SAVE_INQUIRY_COMPLETE;

    constructor(public payload: NewInquiry) {}
}

export type Actions =
    | GetBookingInquiry
    | GetServicePackageDetails
    | ReplaceBookingInquiry
    | ReplaceServicePackageDetails
    | RemoveServicePackageFromInquiry
    | AddServicePackageToInquiry
    | SaveInquiry
    | SaveInquiryComplete
    | SetCurrentVIN
    | ResetBookingInquiry;



