import { Component, Input } from '@angular/core';

@Component({
    selector: 'vwbt-loader',
    template: `
        <div class="loader" *ngIf="loading > 0">
            <i class="fas fa-sync fa-spin"></i>
        </div>
    `,
    styles: [
        `
        .loader {
            background: rgba(0,0,0,0.6);
            border-radius: 10px;
            color: #FFF;
            height: 120px;
            left: 50%;
            line-height: 120px;
            margin-left: -60px;
            position: fixed;
            text-align: center;
            top: 10%;
            width: 120px;
        }
    `
    ]
})
export class LoaderComponent {
    @Input() loading: number;
}



