import { Component, OnInit, ViewChild } from '@angular/core';
import { ServicePackage } from '../../state/booking-inquiry/model/service-package';
import { ApiService } from '../../shared/services/api.service';
import { BookingInquiryStateService } from '../../state/booking-inquiry/services/booking-inquiry-state.service';
import { combineLatest } from 'rxjs/observable/combineLatest';
import { PastInquiry } from '../../state/booking-inquiry/model/past-inquiry';
import { SearchFieldComponent } from './search-field.component';

@Component({
    selector: 'vwbt-search',
    template: `<div class="container">
    <div class="row">
        <div class="col">
            <div class="card mt-4">
                <div class="card-header vin">
                    <div class="col-auto">
                        <vwbt-search-field #searchForm></vwbt-search-field>
                    </div>
                </div>
                <div class="card-body vin-body text-center" *ngIf="servicePackages.length > 0">
                    <p *ngIf="selectedServicePackages.length === 0">
                        <i class="fas fa-inbox"></i> Currently there is no service package selected
                    </p>
                    <vwbt-current-inquiry *ngIf="selectedServicePackages.length > 0"
                        [servicePackages]="selectedServicePackages">
                    </vwbt-current-inquiry>
                </div>
            </div>
        </div>
    </div>
    <div class="vin-message mt-4 row text-center">
        <div class="col">
            <p class="invalid" *ngIf="validationMessage">{{validationMessage}}</p>
            <p class="text-center" *ngIf="!validationMessage && initialState && !vinServerError">
                Enter the <strong>Vehicle identification number</strong>
                to receive relevant service packages and past inquiries concerning
                a vehicle.
            </p>
            <p *ngIf="!initialState && servicePackages.length === 0 && pastInquiries.length === 0 && !vinServerError">
                No service packages or past inquiries could be found for the VIN you entered.
            </p>
            <p *ngIf="vinServerError" class="invalid">
                There was a problem with retrieving the details associated with the VIN.
                Please check your VIN and try again.
            </p>
        </div>
    </div>
</div>`,
    styles: [
        `
            .vin {
                background: #6E747B;
                color: #FFF;
                padding-bottom: 15px;
                padding-top: 15px;
                text-align: center;
            }
            .vin-input {
                text-align: center;
                width: 265px;
            }
            .vin-text {
                display: inline-block;
                margin: 0;
                width: 265px;
            }
            .hidden {
                display: none;
            }
            .btn-submit {
                font-size: 16px;
                width: 90px;
            }
            .invalid {
                color: #dc3545;
            }
            .vin-body {
                background: #E6E6E6;
                padding-top: 30px;
            }
            .vin-body .fa-inbox {
                padding-right: 10px;
            }
        `
    ]
})
export class SearchComponent implements OnInit {
    initialState = true;
    servicePackages: ServicePackage[];
    selectedServicePackages: ServicePackage[];
    pastInquiries: PastInquiry[];
    vinServerError = false;

    @ViewChild('searchForm') searchForm: SearchFieldComponent;

    constructor(private bookingInquiriesStateService: BookingInquiryStateService, private apiService: ApiService) {}

    ngOnInit() {
        this.bookingInquiriesStateService.getCurrentVIN().subscribe(x => (this.initialState = !x));

        combineLatest(
            this.bookingInquiriesStateService.getServicePackages(),
            this.bookingInquiriesStateService.getPastInquiries(),
            (servicePackages: ServicePackage[], pastInquiries: PastInquiry[]) => {
                return {
                    servicePackages: servicePackages,
                    pastInquiries: pastInquiries
                };
            }
        ).subscribe(inquiryResult => {
            this.servicePackages = inquiryResult.servicePackages;
            this.pastInquiries = inquiryResult.pastInquiries;
        });

        combineLatest(
            this.bookingInquiriesStateService.getSelectedPackages(),
            this.bookingInquiriesStateService.getServicePackages(),
            (selectedPackageIds: string[], servicePackages: ServicePackage[]) =>
                servicePackages.filter((sp: ServicePackage) =>
                    selectedPackageIds.some(packageId => packageId === sp.id)
                )
        ).subscribe((selectedPackages: ServicePackage[]) => (this.selectedServicePackages = selectedPackages));

        this.apiService.getVinServerError().subscribe(vinServerError => (this.vinServerError = vinServerError));
    }

    get validationMessage(): string {
        return this.searchForm.validationMessage;
    }
}



