import { Action } from '@ngrx/store';

const prefix = '[LAYOUT]';

export const SHOW_LOADING = `${prefix} Show loading`;
export const HIDE_LOADING = `${prefix} Hide loading`;
export const SHOW_NOTIFICATION = `${prefix} Show notification`;
export const HIDE_NOTIFICATION = `${prefix} Hide notification`;

export class ShowLoading implements Action {
    readonly type = SHOW_LOADING;
}

export class HideLoading implements Action {
    readonly type = HIDE_LOADING;
}

export class ShowNotification implements Action {
    readonly type = SHOW_NOTIFICATION;
}
export class HideNotification implements Action {
    readonly type = HIDE_NOTIFICATION;
}

export type Actions = ShowLoading | HideLoading;



