import { NgModule } from '@angular/core';
import { ReactiveFormsModule } from '@angular/forms';
import { BrowserModule } from '@angular/platform-browser';

import { PastInquiryServicePackageComponent } from './components/past-inquiry-service-package.component';
import { PastInquiryComponent } from './components/past-inquiry.component';
import { SelectedServicePackageComponent } from './components/selected-service-package.component';
import { ServicePackageComponent } from './components/service-package.component';
import { CurrentInquiryComponent } from './containers/current-inquiry.component';
import { SearchResultComponent } from './containers/search-result.component';
import { SearchFieldComponent } from './containers/search-field.component';
import { SearchComponent } from './containers/search.component';

const COMPONENTS = [
    SearchComponent,
    SearchResultComponent,
    ServicePackageComponent,
    PastInquiryComponent,
    CurrentInquiryComponent,
    PastInquiryServicePackageComponent,
    SelectedServicePackageComponent,
    SearchFieldComponent
];

@NgModule({
    declarations: COMPONENTS,
    imports: [BrowserModule, ReactiveFormsModule],
    exports: [SearchComponent, SearchResultComponent]
})
export class BookingInquiryModule {
    static forRoot() {
        return {
            ngModule: BookingInquiryModule
        };
    }
}



