import { Component, OnInit } from '@angular/core';
import { ServicePackage } from '../../state/booking-inquiry/model/service-package';
import { BookingInquiryStateService } from '../../state/booking-inquiry/services/booking-inquiry-state.service';
import { PastInquiry } from '../../state/booking-inquiry/model/past-inquiry';

@Component({
    selector: 'vwbt-search-result',
    template: `
        <div class="container"
            *ngIf="(servicePackages && servicePackages.length > 0) || (pastInquiries && pastInquiries.length > 0)">
            <div class="row mt-3">
                <div class="col">
                    <div class="card mt-3">
                        <div class="card-header">
                            <ul class="nav nav-pills card-header-pills nav-fill">
                                <li class="nav-item nav-service-packages"
                                    (click)="setActiveTabIndex(0)">
                                    <a class="nav-link" href="#">
                                        <i class="fas fa-wrench"></i>
                                        Available service packages
                                    </a>
                                </li>
                                <li class="nav-item nav-past-inquiries"
                                    (click)="setActiveTabIndex(1)">
                                    <a class="nav-link" href="#">
                                        <span class="icon-past-inquiries">
                                            <i class="fas fa-calendar"></i>
                                            <span *ngIf="pastInquiries && pastInquiries.length > 0"
                                                class="badge badge-past-inquiries badge-pill badge-primary">
                                                &nbsp;
                                            </span>
                                        </span>
                                        Past inquiries
                                    </a>
                                </li>
                            </ul>
                        </div>
                        <div class="card-body body-service-packages" *ngIf="activeTabIndex === 0">
                            <div *ngIf="servicePackages && servicePackages.length > 0">
                                <div *ngFor="let servicePackage of servicePackages; trackBy: servicePackageTracker"
                                    class="mt-3">
                                    <vwbt-service-package
                                        [servicePackageSelected]="isPackageSelected(servicePackage.id)"
                                        [servicePackage]="servicePackage"
                                        (getDetails)="handleGetDetails($event)"
                                        (addToInquiry)="handleAddToInquiry($event)">
                                    </vwbt-service-package>
                                </div>
                            </div>
                            <p *ngIf="servicePackages && servicePackages.length === 0">
                                There are no service packages available for the current vehicle.
                            </p>
                        </div>
                        <div class="card-body body-past-inquiries" *ngIf="activeTabIndex === 1">
                            <div *ngIf="pastInquiries && pastInquiries.length > 0">
                                <div *ngFor="let pastInquiry of pastInquiries"
                                    class="mt-3">
                                    <vwbt-past-inquiry
                                        [pastInquiry]="pastInquiry">
                                    </vwbt-past-inquiry>
                                </div>
                            </div>
                            <p *ngIf="pastInquiries && pastInquiries.length === 0">
                                There are no past inquiries available for the current vehicle.
                            </p>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    `,
    styles: [
        `
        .card-header {
            border-bottom: none;
            padding: 0;
        }
        .card-header-pills {
            margin: 0;
        }
        .nav-item {
            padding-bottom: 6px;
            padding-top: 7px;
        }
        .nav-item a {
            color: #000;
        }
        .nav-item i {
            padding-right: 10px;
        }
        .nav-service-packages {
            background: #E6E6E6;
        }
        .nav-past-inquiries {
            background: #FFF;
        }
        .body-service-packages {
            background: #E6E6E6;
        }
        .icon-past-inquiries {
            position: relative;
        }
        .badge-past-inquiries {
            font-size: 9px;
            left: 8px;
            position: absolute;
            top: -4px;
        }
    `
    ]
})
export class SearchResultComponent implements OnInit {
    activeTabIndex = 0;
    servicePackages: ServicePackage[];
    pastInquiries: PastInquiry[];
    selectedPackages: string[];

    constructor(private bookingInquiriesStateService: BookingInquiryStateService) {}

    ngOnInit() {
        this.bookingInquiriesStateService
            .getServicePackages()
            .subscribe(servicePackages => (this.servicePackages = servicePackages));
        this.bookingInquiriesStateService
            .getPastInquiries()
            .subscribe(pastInquiries => (this.pastInquiries = pastInquiries));
        this.bookingInquiriesStateService
            .getSelectedPackages()
            .subscribe(selectedPackages => (this.selectedPackages = selectedPackages));
    }

    isPackageSelected(id: string): boolean {
        return this.selectedPackages.some(x => x === id);
    }

    setActiveTabIndex(index: number) {
        this.activeTabIndex = index;
    }

    servicePackageTracker(index, servicePackage) {
        return servicePackage.id;
    }

    handleGetDetails(id: string) {
        this.bookingInquiriesStateService.fetchServicePackageDetails(id);
    }

    handleAddToInquiry(id: string) {
        this.bookingInquiriesStateService.addServicePackageToInquiry(id);
    }
}



