import { Injectable } from '@angular/core';
import { Store } from '@ngrx/store';
import { State } from '../reducers';
import * as layoutActions from '../actions/layout';
import * as coreReducers from '../reducers/index';
import { Observable } from 'rxjs/Observable';

@Injectable()
export class CoreStateService {
    constructor(private _store: Store<State>) {}

    // Layout observables
    getLoading(): Observable<number> {
        return this._store.select(coreReducers.getLoading);
    }

    // Layout actions
    showLoading() {
        this._store.dispatch(new layoutActions.ShowLoading());
    }

    hideLoading() {
        this._store.dispatch(new layoutActions.HideLoading());
    }
}



