import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { ServicePackage } from '../../state/booking-inquiry/model/service-package';
import { Observable } from 'rxjs/Observable';
import { BehaviorSubject } from 'rxjs/BehaviorSubject';
import { BookingInquiry } from '../../state/booking-inquiry/model/booking-inquiry';
import { NewInquiry } from '../../state/booking-inquiry/model/new-inquiry';

@Injectable()
export class ApiService {
    vinServerError = new BehaviorSubject<boolean>(false);

    constructor(private http: HttpClient) {}

    getBookingInquiry(vin: string): Observable<BookingInquiry> {
        this.setVinServerError(false);
        return this.http.get<BookingInquiry>(`/api/vin/${vin}`);
    }

    getPackageDetails(id: string): Observable<ServicePackage> {
        return this.http.get<ServicePackage>(`/api/servicepackages/${id}/details`);
    }

    saveInquiry(inquiry: NewInquiry): Observable<any> {
        return this.http.post(
            `/api/inquiries/${inquiry.vin}`,
            inquiry.packageIds.map(packageId => {
                return { id: packageId };
            })
        );
    }

    setVinServerError(state: boolean): void {
        this.vinServerError.next(state);
    }

    getVinServerError(): Observable<boolean> {
        return this.vinServerError;
    }
}



