import { Component, Input, Output, EventEmitter } from '@angular/core';
import { ServicePackage } from '../../state/booking-inquiry/model/service-package';

@Component({
    selector: 'vwbt-service-package',
    template: `
        <div class="card">
            <h5 class="card-header justify-content-between" (click)="getDetailsClicked($event, servicePackage.id)">
                {{ servicePackage.name }}
                <button type="button" class="btn btn-primary btn-add-to-inquiry"
                    [ngClass]="{'disabled': servicePackageSelected}"
                    (click)="addToInquiryClicked($event, servicePackage.id)">
                    Add to inquiry
                </button>
                <span class="price mr-4">Price: {{ servicePackage.price | number:'1.2-2':'de' }} €</span>
            </h5>
            <div class="card-body" *ngIf="showDetails && !detailsMissing">
                <div *ngIf="!detailsMissing">
                    <p>Description: {{ servicePackage.description }}</p>
                    <div class="row">
                        <div class="col">
                            <p>Spare parts</p>
                            <ul>
                                <li *ngFor="let part of servicePackage.parts">{{part}}</li>
                            </ul>
                        </div>
                        <div class="col">
                            <p>Labour</p>
                            <ul>
                                <li *ngFor="let labourItem of servicePackage.labour">{{labourItem}}</li>
                            </ul>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    `,
    styles: [
        `
        h5 {
            cursor: pointer;
            font-size: 18px;
            line-height: 35px;
            padding-bottom: 5px;
            padding-top: 5px;
        }
        .btn-add-to-inquiry {
            float: right;
            width: 184px;
        }
        .card {
            border: none;
        }
        .card-header {
            background: #fff;
            border: none;
        }
        .card-body {
            border-top: 1px solid #E6E6E6;
        }
        .price {
            float: right;
        }
    `
    ]
})
export class ServicePackageComponent {
    showDetails = false;

    @Input() servicePackage: ServicePackage;
    @Input() servicePackageSelected = false;

    @Output() getDetails = new EventEmitter();
    @Output() addToInquiry = new EventEmitter();

    get detailsMissing(): boolean {
        return !(this.servicePackage.description || this.servicePackage.labour || this.servicePackage.parts);
    }

    getDetailsClicked(event: Event, id: string) {
        this.showDetails = !this.showDetails;
        event.stopPropagation();

        if (!this.detailsMissing) {
            return;
        }

        this.getDetails.emit(id);
    }

    addToInquiryClicked(event: Event, id: string) {
        event.stopPropagation();

        if (this.detailsMissing) {
            this.getDetails.emit(id);
        }

        this.addToInquiry.emit(id);
    }
}



