import { ServicePackage } from '../model/service-package';
import * as servicePackages from '../actions/booking-inquiry';
import { PastInquiry } from '../model/past-inquiry';

export interface State {
    servicePackages: ServicePackage[];
    pastInquiries: PastInquiry[];
    selectedPackages: string[];
    VIN: string;
    isCurrentInquirySaved: boolean;
}

const initialState: State = {
    servicePackages: [],
    pastInquiries: [],
    selectedPackages: [],
    VIN: '',
    isCurrentInquirySaved: false
};

export function reducer(state: State = initialState, action: servicePackages.Actions): State {
    switch (action.type) {
        case servicePackages.REPLACE_BOOKING_INQUIRY: {
            return {
                ...state,
                servicePackages: (action as servicePackages.ReplaceBookingInquiry).payload.servicePackages,
                pastInquiries: (action as servicePackages.ReplaceBookingInquiry).payload.pastInquiries
            };
        }

        case servicePackages.RESET_BOOKING_INQUIRY: {
            return {
                ...state,
                servicePackages: [],
                pastInquiries: []
            };
        }

        case servicePackages.SAVE_INQUIRY_COMPLETE: {
            const selectedServicePackages: ServicePackage[] = [];
            const packageIds = (action as servicePackages.SaveInquiryComplete).payload.packageIds;

            selectedServicePackages.push(
                ...state.servicePackages.filter(servicePackage => packageIds.some(id => id === servicePackage.id))
            );

            const savedInquiry: PastInquiry = {
                date: new Date(),
                price: selectedServicePackages.map(x => x.price).reduce((x, y) => x + y, 0),
                servicePackages: selectedServicePackages
            };

            const newPastInquiries: PastInquiry[] = [];
            newPastInquiries.push(savedInquiry);
            newPastInquiries.push(...state.pastInquiries);

            return {
                ...state,
                pastInquiries: newPastInquiries,
                isCurrentInquirySaved: true
            };
        }

        case servicePackages.REPLACE_SERVICE_PACKAGE_DETAILS: {
            return {
                ...state,
                servicePackages: state.servicePackages.map(
                    (p: ServicePackage) =>
                        p.id === (action as servicePackages.ReplaceServicePackageDetails).payload.id
                            ? (action as servicePackages.ReplaceServicePackageDetails).payload
                            : p
                )
            };
        }

        case servicePackages.ADD_SERVICE_PACKAGE_TO_INQUIRY: {
            const newSelectedPackages = [];

            newSelectedPackages.push(
                ...state.selectedPackages,
                (action as servicePackages.AddServicePackageToInquiry).payload
            );

            return {
                ...state,
                selectedPackages: newSelectedPackages,
                isCurrentInquirySaved: false
            };
        }

        case servicePackages.REMOVE_SERVICE_PACKAGE_FROM_INQUIRY: {
            const newSelectedPackages = [];

            newSelectedPackages.push(
                ...state.selectedPackages.filter(
                    x => x !== (action as servicePackages.RemoveServicePackageFromInquiry).payload
                )
            );

            return {
                ...state,
                selectedPackages: newSelectedPackages,
                isCurrentInquirySaved: false
            };
        }

        case servicePackages.RESET_SERVICE_PACKAGE_SELECTION: {
            return {
                ...state,
                selectedPackages: []
            };
        }

        case servicePackages.SET_CURRENT_VIN: {
            return {
                ...state,
                VIN: (action as servicePackages.SetCurrentVIN).payload
            };
        }

        default: {
            return state;
        }
    }
}

export const getServicePackages = (state: State) => state.servicePackages;
export const getPastInquiries = (state: State) => state.pastInquiries;
export const getSelectedPackages = (state: State) => state.selectedPackages;
export const getCurrentVIN = (state: State) => state.VIN;
export const getIsCurrentInquirySaved = (state: State) => state.isCurrentInquirySaved;



