import { Component, ViewChild, ElementRef, OnInit } from '@angular/core';
import { FormGroup, FormControl, Validators, NgForm } from '@angular/forms';
import * as vinValidators from '../validators/vin-validators';
import { BookingInquiryStateService } from '../../state/booking-inquiry/services/booking-inquiry-state.service';
import { ApiService } from '../../shared/services/api.service';

@Component({
    selector: 'vwbt-search-field',
    template: `
    <form [formGroup]="vinFormGroup" class="form-inline justify-content-center"
        (ngSubmit)="search()" #form="ngForm">
        <div class="form-group mr-2">
            <label for="vwbt-vin-input">
                <i class="fas fa-car"></i>
            </label>
        </div>
        <div class="form-group mr-2">
            <input [ngClass]=
                    "{'hidden': !vinInputMode,
                    'is-invalid': ((!form.valid && form.submitted) || vinServerError)}"
                #searchInput
                formControlName="vin" class="vin-input form-control"
                id="vwbt-vin-input" placeholder="VIN" type="text" maxlength="17" />
            <p *ngIf="!vinInputMode" class="vin-text">{{ vinSearchTerm }}</p>
        </div>
        <div class="form-group">
            <button *ngIf="vinInputMode" type="submit"
                class="btn btn-submit btn-outline-light">
                Confirm
            </button>
            <button *ngIf="!vinInputMode" type="button"
                class="btn btn-submit btn-outline-light"
                (click)="resetSearch()">
                Change
            </button>
        </div>
    </form>
    `,
    styles: [
        `
            .vin {
                background: #6E747B;
                color: #FFF;
                padding-bottom: 15px;
                padding-top: 15px;
                text-align: center;
            }
            .vin-input {
                text-align: center;
                width: 265px;
            }
            .vin-text {
                display: inline-block;
                margin: 0;
                width: 265px;
            }
            .hidden {
                display: none;
            }
            .btn-submit {
                font-size: 16px;
                width: 90px;
            }
            .invalid {
                color: #dc3545;
            }
            .vin-body {
                background: #E6E6E6;
                padding-top: 30px;
            }
            .vin-body .fa-inbox {
                padding-right: 10px;
            }
        `
    ]
})
export class SearchFieldComponent implements OnInit {
    vinSearchTerm: string;
    vinFormGroup: FormGroup;
    vinServerError = false;
    bookingInquiryResultExists = false;

    @ViewChild('form') vinForm: NgForm;
    @ViewChild('searchInput') searchInput: ElementRef;

    get vinInputMode(): boolean {
        return !this.vinSearchTerm || !this.bookingInquiryResultExists;
    }

    get vinFormValue(): string {
        return this.vinFormGroup.controls.vin.value;
    }

    constructor(private bookingInquiriesStateService: BookingInquiryStateService, private apiService: ApiService) {
        this.vinFormGroup = new FormGroup(
            {
                vin: new FormControl('', [
                    Validators.required,
                    Validators.minLength(17),
                    vinValidators.firstSixCharsShouldBeLetters(),
                    vinValidators.lastSixCharsShouldBeNumbers(),
                    vinValidators.noSpecialSign()
                ])
            },
            { updateOn: 'submit' }
        );
    }

    ngOnInit() {
        this.bookingInquiriesStateService.getCurrentVIN().subscribe(vin => (this.vinSearchTerm = vin));
        this.bookingInquiriesStateService
            .bookingInquiryResultExists()
            .subscribe(x => (this.bookingInquiryResultExists = x));
        this.apiService.getVinServerError().subscribe(vinServerError => (this.vinServerError = vinServerError));
    }

    resetSearch() {
        this.searchInput.nativeElement.value = '';
        this.bookingInquiriesStateService.resetBookingInquiries();
    }

    search() {
        if (this.vinFormGroup.invalid) {
            return;
        }

        this.bookingInquiriesStateService.fetchBookingInquiries(this.vinFormValue);
        this.bookingInquiriesStateService.setCurrentVIN(this.vinFormValue);
    }

    get validationMessage(): string {
        if (this.vinFormGroup.valid || !this.vinForm.submitted) {
            return '';
        }

        const errors = Object.keys(this.vinFormGroup.controls.vin.errors);

        if (errors.length > 1) {
            return `You have entered an incorrect VIN. \
Please enter a VIN according to this example: WVWZZZ1J3W3867502.`;
        }

        switch (errors[0]) {
            case 'required':
            case 'minlength':
                return 'The VIN must be 17 characters long.';
            case 'firstSixCharsError':
                return 'The first six characters of the VIN must be letters.';
            case 'lastSixCharsError':
                return 'The last six characters must be numeric.';
            case 'noSpecialSignError':
                return 'The VIN consists only of letters and numbers.';
        }
    }
}



