import { Component, Input, Output, EventEmitter } from '@angular/core';
import { ServicePackage } from '../../state/booking-inquiry/model/service-package';

@Component({
    selector: 'vwbt-selected-service-package',
    template: `
        <div class="card mb-3">
            <h5 class="card-header" (click)="toggleDetails($event)">
                <span class="name">{{ servicePackage.name }}</span>
                <span class="remove">
                    <i class="far fa-trash-alt"
                        (click)="removeFromInquiryClicked($event, servicePackage.id)">
                    </i>
                </span>
                <span class="price mr-4">Price: {{ servicePackage.price | number:'1.2-2':'de' }} €</span>
            </h5>
            <div class="card-body text-left" *ngIf="showDetails">
                <div>
                    <p>Description: {{ servicePackage.description }}</p>
                    <div class="row">
                        <div class="col">
                            <p>Spare parts</p>
                            <ul>
                                <li *ngFor="let part of servicePackage.parts">{{part}}</li>
                            </ul>
                        </div>
                        <div class="col">
                            <p>Labour</p>
                            <ul>
                                <li *ngFor="let labourItem of servicePackage.labour">{{labourItem}}</li>
                            </ul>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    `,
    styles: [
        `
        h5 {
            cursor: pointer;
            font-size: 18px;
            line-height: 35px;
            padding-bottom: 5px;
            padding-top: 5px;
        }
        .name {
            float: left;
        }
        .btn-add-to-inquiry {
            float: right;
            width: 184px;
        }
        .card {
            border: none;
        }
        .card-header {
            background: #fff;
            border: none;
        }
        .card-body {
            border-top: 1px solid #E6E6E6;
        }
        .price,
        .remove {
            float: right;
        }
    `
    ]
})
export class SelectedServicePackageComponent {
    showDetails = false;

    @Input() servicePackage: ServicePackage;
    @Output() removeFromInquiry = new EventEmitter();

    toggleDetails(event: Event) {
        this.showDetails = !this.showDetails;
        event.stopPropagation();
    }

    removeFromInquiryClicked(event: Event, id: string) {
        event.stopPropagation();
        this.removeFromInquiry.emit(id);
    }
}



