import { Injectable } from '@angular/core';
import { Effect, Actions } from '@ngrx/effects';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/mergeMap';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/catch';

import { Action } from '@ngrx/store';
import { ApiService } from '../../../shared/services/api.service';
import * as bookingInquiryActions from '../actions/booking-inquiry';
import * as notification from '../../core/actions/notification';
import * as layout from '../../core/actions/layout';
import { NotificationType } from '../../core/model/notification-type';

@Injectable()
export class SaveInquiryEffects {
    constructor(private apiService: ApiService, private actions$: Actions) {}

    @Effect()
    get$: Observable<Action> = this.actions$.ofType(bookingInquiryActions.SAVE_INQUIRY).mergeMap(action =>
        this.apiService
            .saveInquiry((action as bookingInquiryActions.SaveInquiry).payload)
            .map(
                () =>
                    new bookingInquiryActions.SaveInquiryComplete((action as bookingInquiryActions.SaveInquiry).payload)
            )
            .catch(() => {
                this.apiService.setVinServerError(true);

                return Observable.of(
                    new notification.Set({
                        message: 'There was an error while loading data. Please try again.',
                        type: NotificationType.ERROR
                    }),
                    new layout.HideLoading()
                );
            })
    );
}



