import { Component, Input } from '@angular/core';
import { ServicePackage } from '../../state/booking-inquiry/model/service-package';

@Component({
    selector: 'vwbt-past-inquiry-service-package',
    template: `
        <div class="card mb-3">
            <h5 class="card-header package-header" (click)="toggleCard()">
                {{ servicePackage.name }}
            </h5>
            <div class="card-body package-body" *ngIf="cardOpen">
                <div>
                    <p>Description: {{ servicePackage.description }}</p>
                    <div class="row">
                        <div class="col">
                            <p>Spare parts</p>
                            <ul>
                                <li *ngFor="let part of servicePackage.parts">{{part}}</li>
                            </ul>
                        </div>
                        <div class="col">
                            <p>Labour</p>
                            <ul>
                                <li *ngFor="let labourItem of servicePackage.labour">{{labourItem}}</li>
                            </ul>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    `,
    styles: [
        `
        .card {
            border: none;
        }
        .package-header {
            background: #fff;
            border: none;
            cursor: pointer;
        }
        .package-body {
            border-top: 1px solid #E6E6E6;
        }
    `
    ]
})
export class PastInquiryServicePackageComponent {
    @Input() servicePackage: ServicePackage;

    cardOpen = false;

    constructor() {}

    toggleCard() {
        this.cardOpen = !this.cardOpen;
    }
}



