import { Component, Input, OnInit } from '@angular/core';
import { ServicePackage } from '../../state/booking-inquiry/model/service-package';
import { BookingInquiryStateService } from '../../state/booking-inquiry/services/booking-inquiry-state.service';
import { NewInquiry } from '../../state/booking-inquiry/model/new-inquiry';
import { Observable } from 'rxjs/Observable';

@Component({
    selector: 'vwbt-current-inquiry',
    template: `
        <vwbt-selected-service-package
            *ngFor="let servicePackage of servicePackages"
            [servicePackage]="servicePackage"
            (removeFromInquiry)="removeFromInquiry($event)">
        </vwbt-selected-service-package>
        <button type="button" class="btn btn-primary btn-add-to-inquiry"
            (click)="saveInquiry()"
            [disabled]="isCurrentInquirySaved | async">
            Save inquiry
        </button>
        <p class="price mr-3">
            Total price: {{ totalPrice | number:'1.2-2':'de' }} €
        </p>
    `,
    styles: [
        `
        .btn-add-to-inquiry {
            float: right;
        }
        .price {
            float: right;
            line-height: 40px;
            margin: 0;
        }
    `
    ]
})
export class CurrentInquiryComponent implements OnInit {
    isCurrentInquirySaved: Observable<boolean>;
    vin: string;

    @Input() servicePackages: ServicePackage[];

    constructor(private bookingInquiriesStateService: BookingInquiryStateService) {}

    ngOnInit() {
        this.bookingInquiriesStateService.getCurrentVIN().subscribe(vin => (this.vin = vin));
        this.isCurrentInquirySaved = this.bookingInquiriesStateService.isCurrentInquirySaved();
    }

    get totalPrice() {
        return this.servicePackages.map(x => x.price).reduce((x, y) => x + y, 0);
    }

    removeFromInquiry(id: string) {
        this.bookingInquiriesStateService.removeServicePackageFromInquiry(id);
    }

    saveInquiry() {
        this.bookingInquiriesStateService.saveInquiry({
            packageIds: this.servicePackages.map(p => p.id),
            vin: this.vin
        } as NewInquiry);
    }
}



