import * as notification from '../actions/notification';
import { NotificationType } from '../model/notification-type';
import { Notification } from '../model/notification';

export type State = Notification;

const initialState: State = {
    message: '',
    type: NotificationType.INFO
};

export function reducer(state: State = initialState, action: notification.Actions): State {
    switch (action.type) {
        case notification.SET: {
            return { ...state, ...(action as notification.Set).payload };
        }

        default: {
            return state;
        }
    }
}



