import { Component, Input } from '@angular/core';
import { PastInquiry } from '../../state/booking-inquiry/model/past-inquiry';

@Component({
    selector: 'vwbt-past-inquiry',
    template: `
        <div class="card">
            <h5 class="card-header inquiry-header">
                <span>Inquiry: {{ pastInquiry.date | date:'dd.MM.yyyy HH:mm' }}</span>
                <span class="total-price">
                    Total price: {{ pastInquiry.price | number:'1.2-2':'de' }} €
                </span>
            </h5>
            <div class="card-body inquiry-body">
                <vwbt-past-inquiry-service-package
                    *ngFor="let servicePackage of pastInquiry.servicePackages"
                    [servicePackage]="servicePackage">
                </vwbt-past-inquiry-service-package>
            </div>
        </div>
    `,
    styles: [
        `
        .card {
            border: none;
        }
        .inquiry-header {
            background: #6E747B;
            border-bottom: 1px solid #6E747B;
            color: #fff;
            font-size: 18px;
            padding-bottom: 20px;
            padding-top: 20px;
        }
        .inquiry-body {
            background: #E6E6E6;
        }
        .total-price {
            float: right;
        }
    `
    ]
})
export class PastInquiryComponent {
    @Input() pastInquiry: PastInquiry;

    constructor() {}
}



