import { InjectionToken, NgModule } from '@angular/core';
import { ActionReducer, ActionReducerMap, MetaReducer, StoreModule } from '@ngrx/store';
import * as fromCore from './core/reducers';
import * as fromServicePackages from './booking-inquiry/reducers';
import { EffectsModule } from '@ngrx/effects';
import { environment } from '../../environments/environment';
import { ServicePackageDetailsEffects } from './booking-inquiry/effects/details';
import { BookingInquiryEffects } from './booking-inquiry/effects/booking-inquiry';
import { SaveInquiryEffects } from './booking-inquiry/effects/save-inquiry';

export type AppState = fromCore.State | fromServicePackages.State;

export const reducers: ActionReducerMap<AppState> = {
    ...fromCore.reducers,
    ...fromServicePackages.reducers
};

export function logger(reducer: ActionReducer<AppState>): ActionReducer<AppState> {
    return function(state: AppState, action: any): AppState {
        console.log('++++++++++ STATE CHANGE:', action.type);
        console.log('OLD STATE:', state);
        console.log('PAYLOAD:', action.payload);

        const newState = reducer(state, action);
        console.log('NEW STATE:', newState);

        return newState;
    };
}

// Workaround for ngrx AOT issues (https://github.com/ngrx/platform/issues/116)
export const REDUCERS_TOKEN = new InjectionToken<ActionReducerMap<AppState>>('Registered Reducers');

Object.assign(REDUCERS_TOKEN, reducers);

export const metaReducers: MetaReducer<AppState>[] = !environment.production ? [logger] : [];

@NgModule({
    imports: [
        StoreModule.forRoot(REDUCERS_TOKEN, { metaReducers: metaReducers }),
        EffectsModule.forRoot([ServicePackageDetailsEffects, BookingInquiryEffects, SaveInquiryEffects])
        /* ),
        StoreDevtoolsModule.instrument({ maxAge: 10 }) */
    ],
    providers: [{ provide: REDUCERS_TOKEN, useValue: reducers }]
})
export class AppStore {}



