import { Injectable } from '@angular/core';
import { Store } from '@ngrx/store';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/observable/of';
import { ServicePackage } from '../model/service-package';
import * as bookingInquiryActions from '../actions/booking-inquiry';
import * as bookingInquiryReducer from '../reducers';
import { PastInquiry } from '../model/past-inquiry';
import { NewInquiry } from '../model/new-inquiry';
import { combineLatest } from 'rxjs/observable/combineLatest';

@Injectable()
export class BookingInquiryStateService {
    constructor(private _store: Store<any>) {}

    // Service packages observables
    getPastInquiries(): Observable<PastInquiry[]> {
        return this._store.select(bookingInquiryReducer.getPastInquiries);
    }

    getServicePackages(): Observable<ServicePackage[]> {
        return this._store.select(bookingInquiryReducer.getServicePackageList);
    }

    bookingInquiryResultExists(): Observable<boolean> {
        return combineLatest(
            this.getServicePackages(),
            this.getPastInquiries(),
            (y: ServicePackage[], x: PastInquiry[]) => x.length > 0 || y.length > 0
        );
    }

    isCurrentInquirySaved(): Observable<boolean> {
        return this._store.select(bookingInquiryReducer.getIsCurrentInquirySaved);
    }

    getSelectedPackages(): Observable<string[]> {
        return this._store.select(bookingInquiryReducer.getSelectedPackages);
    }

    getCurrentVIN(): Observable<string> {
        return this._store.select(bookingInquiryReducer.getCurrentVIN);
    }

    setCurrentVIN(vin: string): void {
        return this._store.dispatch(new bookingInquiryActions.SetCurrentVIN(vin));
    }

    // Service package actions
    fetchBookingInquiries(vin: string): void {
        this._store.dispatch(new bookingInquiryActions.GetBookingInquiry(vin));
    }

    fetchServicePackageDetails(id: string): void {
        this._store.dispatch(new bookingInquiryActions.GetServicePackageDetails(id));
    }

    addServicePackageToInquiry(id: string): void {
        this._store.dispatch(new bookingInquiryActions.AddServicePackageToInquiry(id));
    }

    removeServicePackageFromInquiry(id: string): void {
        this._store.dispatch(new bookingInquiryActions.RemoveServicePackageFromInquiry(id));
    }

    resetBookingInquiries(): void {
        this._store.dispatch(new bookingInquiryActions.ResetBookingInquiry());
        this._store.dispatch(new bookingInquiryActions.ResetServicePackageSelection());
        this._store.dispatch(new bookingInquiryActions.SetCurrentVIN(''));
    }

    saveInquiry(inquiry: NewInquiry): void {
        this._store.dispatch(new bookingInquiryActions.SaveInquiry(inquiry));
    }
}



