import { NgModule } from '@angular/core';
import { AppComponent } from './containers/app.component';
import { BrowserModule } from '@angular/platform-browser';
import { SharedModule } from '../shared/shared.module';
import { StateModule } from '../state/state.module';
import { HeaderComponent } from './components/header.component';
import { BookingInquiryModule } from '../booking-inquiry/booking-inquiry.module';
import { LoaderComponent } from './components/loader.component';

@NgModule({
    declarations: [AppComponent, HeaderComponent, LoaderComponent],
    imports: [BrowserModule, BookingInquiryModule.forRoot(), StateModule.forRoot(), SharedModule.forRoot()]
})
export class CoreModule {
    static forRoot() {
        return {
            ngModule: CoreModule
        };
    }
}



